import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader

import BEM from '../images/Outdoors/BEM.png';
import Cards from '../images/Outdoors/cards.png';
import Variables from '../images/Outdoors/variables.png';
import outdoors from '../images/outdoors.png';

import { OutdoorsData } from '../../src/data/data.js';

import ModalVideo from 'react-modal-video';
import '../../node_modules/react-modal-video/scss/modal-video.scss';

import '../css/project-details.scss';


class WogProject extends Component {
     constructor(){
        super();
        
        this.state = {
            isOpen: false,
            id: '',
        }
    }

    goTo = (link) => {
        window.open(link)
    }

    openModal = () => {
        this.setState({ isOpen: true })
    }

    render() {
    const { history } = this.props;
    return (
    <>
    <ModalVideo 
        channel='youtube' 
        isOpen={this.state.isOpen} 
        videoId={this.state.id} 
        onClose={ () => this.setState({ isOpen: false, id: '' })}
    />
    <div id='general-container'>
        <div id='modal-container'>
            <button 
                className='button'
                onClick={ () => { this.setState({ id: OutdoorsData.videoId}) ; this.openModal() }}> 
            PROJECT VIDEO 
            </button>
            <button 
                className='button' 
                onClick={ () => this.goTo(OutdoorsData.website)} > 
            GO TO WEBSITE 
            </button>
            <button 
                className='button' 
                onClick={ () => this.goTo(OutdoorsData.github)} > 
            GITHUB REPOSITORY 
            </button>
        </div>
        <div id="detailled-project-container">
            <div id="left-container-div">
                <Carousel>
                    <div>
                        <img src={outdoors} />
                        <p className="legend"> First section of the website </p>
                    </div>
                    <div>
                        <img src={BEM} />
                        <p className="legend">BEM Architecture</p>
                    </div>
                    <div>
                        <img src={Cards} />
                        <p className="legend">Cards with offers</p>
                    </div>
                    <div>
                        <img src={Variables} />
                        <p className="legend">Variables created with SCSS </p>
                    </div>
                </Carousel>
            </div>
            <div id="right-container-div">
                <h1 id="description-title"> Summary of {OutdoorsData.title} </h1>
                <p className="text"> { OutdoorsData.Challenge } </p>
                <p className="text"> { OutdoorsData.Solution } </p>
                <p className="text"> { OutdoorsData.Outcome } </p>
            </div>
        </div>

        <button className='button' onClick={ () => history.push('/')} id='return-to-homepage'> 
            Return to home page 
        </button>
    </div>
    </>
    )
}
}

export default withRouter(WogProject);