import React, { Component } from 'react';
import { Route, Switch, BrowserRouter as Router, withRouter  } from 'react-router-dom';

import WogProject from './pages/wog-project.component.jsx';
import SmartbrainProject from './pages/smartbrain-project.component.jsx';
import OutdoorsProject from './pages/outdoors-project.component.jsx';
import UclothingProject from './pages/uclothing-project.component.jsx';
import FooterAlt from './pages/Layouts/footer-alt.js';


// Import css
import './css/pe-icon-7-stroke.css';
import './css/materialdesignicons.min.css';
import './css/waves.css';
import './css/bootstrap.min.css';
import './css/magnific-popup.css';
import './css/style.css';
import './css/colors/red.css';

const Index1 = React.lazy(() => import('./pages/index-1'));


class App extends Component {
 
  render() {
    return (
      <React.Fragment>
        <Router>
          <React.Suspense fallback={<div>&nbsp;</div>}>
            <Switch>
                <Route exact path="/" component={Index1} /> 
                <Route path="/W.O.G" component={WogProject} />
                <Route path="/Smart-Brain" component={SmartbrainProject} />
                <Route path="/Outdoors" component={OutdoorsProject} />
                <Route path="/U-Clothing" component={UclothingProject} />
            </Switch>
          </React.Suspense>
        <FooterAlt />
        </Router>        
      </React.Fragment>
    );
  }
}



export default withRouter(App);


